@charset "UTF-8";
//body {overflow:hidden;}
// 갤러리
.gallery {
    width:100%; min-width:1280px; position:relative; background-color:$navy; overflow:hidden;
    .sec1 {
        width:100%; margin:0 auto; overflow:hidden;
        @include boxSizing;
        // slide
        .slide_container {
            min-width:1280px; max-width:1580px; margin:0 auto; position:relative; padding:280px 50px 0;
        }
        #gallerySlide {
            width:1580px; margin:0 auto; overflow:visible; padding:0 0 0 20px; position:relative; top:-15px;
            //cursor:none!important;
            &:before {
                width:100%; height:100%; content:""; display:block; background-color:$navy;
                position:absolute; top:0; right:1580px; z-index:100;
            }
            img {@include transition(all, .5s);}
            .gallery_slide {
                width:auto; position:relative; overflow:hidden;
                &:hover {
                    img {@include transform(scale(1.05));}
                }
            }
            .txt_box {
                position:absolute; bottom:54px; left:50px;
                @include transform(skew(-0.03deg));
            }
            span { display:block; color:$white; opacity:.6; font-size:14px; letter-spacing:1px; }
            strong { display:block; padding:15px 0 0; color:$white; font-size:16px; }
        }
        .top_txt { display:inline-block; font-size:14px; color:#75777c; letter-spacing:1px; }
        h1 {
            width:fit-content; font-size:160px;z-index:2;color:#fff;left:-6px;position:relative;top:20px;
            opacity:0;
            @include transform(translate(20px, 0));
        }
    }
    .sec2 {
        min-width:1280px; width:100%; background-color:$navy;
        img { width:103px; height:103px; padding:170px 0 0; }
        .txt_wrap {
            width:950px; margin:0 auto; text-align:center; padding:0 0 220px;
            p { padding:50px 0 0; color:$goldYellow; font-size:24px; }
            h1 {
                opacity:1; width:fit-content; margin:0 auto; padding:30px 0 75px; font-size:50px; color:$white; position:relative;
                &:after {content:"";display:block;width:100%;height:50px;background-color:$navy;position:absolute;top:85px;z-index:2;}
                .title_txt {display:flex;}
                i {
                    display:inline-block; position:relative; top:54px;
                    &.space {margin:0 0 0 10px;}
                }
            }
            span {
                position:relative; color:$white; font-size:18px; display:block; width:fit-content; margin:0 auto;
                &:after, &:before { content:"-"; display:block; position:absolute; top:0; }
                &:after { right:-17px; }
                &:before { left:-17px; }
            }
            strong { display:block; padding:35px 0 0; font-size:72px; color:$goldYellow; }
            .motion-cont {
                &.cont1 {@include transform(translate(0, -20px));}
                &.cont2 {@include transform(translate(0, 20px));}
            }
        }
    }
    .cursor_item {
        width:180px; height:180px;position:absolute; top:0; left:0; z-index:11;display:none;
        background-color:rgba(0, 0, 0, .2);margin:-90px;border:1px solid $goldYellow;pointer-events:none;
        @include borderRadius(100px);
        &.active {
            span {
                &:nth-of-type(1) {@include transform(translate(-15px, -50%));}
                &:nth-of-type(2) {@include transform(translate(5px, -50%));}
            }
        }
        span {
            width:11px; height:20px;display:block;position:absolute; left:50%; top:50%;
            @include transition(all, .3s);
            &:nth-of-type(1) {background:url(../../images/common/arrow_prev.png) 0 0 no-repeat;
                @include transform(translate(-30px, -50%));
            }
            &:nth-of-type(2) {background:url(../../images/common/arrow_next.png) 0 0 no-repeat;
                @include transform(translate(20px, -50%));
            }
        }
    }
    .pop_container {width:0%;height:100vh;position:fixed;left:0;overflow:hidden;top:0;z-index:10;}
    #slidePopup {
        width:1920px; height:100vh; background-color:#171b20; top:0; left:0;position:absolute;
        .popup_wrap {
            width:100%; min-width:1280px; max-width:1580px; height:100%; margin:0 auto;padding:0 50px;
            align-items:center; position:absolute; left:50%; display:none;
            @include transform(translate(-50%, 0));
            @include boxSizing;
            &:first-of-type {display:flex;}
        }
        .txt_wrap {
            position:relative; z-index:2;
            ul {
                width:780px; height:727px;position:relative;
                li {
                    position:absolute; top:0; left:0; opacity:0;
                    &:first-child { opacity:1; }
                    &:nth-of-type(1) {
                        .dip_desc:before {content:"1";}
                    }
                    &:nth-of-type(2) {
                        .dip_desc:before {content:"2";}
                    }
                    &:nth-of-type(3) {
                        .dip_desc:before {content:"3";}
                    }
                    &:nth-of-type(4) {
                        .dip_desc:before {content:"4";}
                    }
                }
            }
            span {color:#8c8d8e; font-size:14px;}
            strong { padding:20px 0 0; display:block; font-size:160px; color:$white; position:relative; left:-9px;}
            p {margin:95px 0 0; line-height:36px; font-size:16px; color:$white;@include transform(skew(-0.03deg));}
            .dip_desc {
                padding:0 0 0 65px; position:absolute;left:-7px;top:670px;
                @include transform(skew(-0.03deg));
                &:before {
                    text-align:center; width:40px; height:40px; padding:11px 0; content:""; display:block;
                    position:absolute; left:0; top:58%; border:1px solid $goldYellow; color:$white; font-size:16px;
                    @include transform(translate(0, -50%));
                    @include boxSizing;
                    @include borderRadius(40px);
                }
                p { margin:0; color:#8c8d8e; font-size:14px; line-height:25px; }
                span { line-height:20px; font-size:16px; font-weight:$bold; color:$white; }
            }
        }
        .transform_img { position:relative; }
        .img_wrap {
            width:1190px; overflow:hidden; height:727px; position:absolute; left:530px; z-index:1;
            img { width:1190px; height:727px; }
        }
        .pop_close {
            width:120px; height:120px; display:block; background-color:#a8835b; position:absolute; left:0; top:0;
            @include transition(all, .2s);
            &:after {
                width:20px; height:20px; content:""; display:block; position:absolute; left:50%; top:50%;
                background:url(../../images/common/x_icon.png) 0 0 no-repeat; background-size:cover;
                @include transform(translate(-50%, -50%));
                @include transition(all, .2s);
            }
            &:hover {
                box-shadow:0 0 100px 100px rgba(5, 5, 5, .2) inset;
                &:after {@include transform(translate(-50%, -50%) rotate(90deg));}
            }
        }
    }
}
.household {
    width:100%;
    .sec1 {
        background-color:$navy;
        width:100%; min-width:1280px; margin:0 auto; padding:280px 50px 0;
        @include boxSizing;
        .txt_wrap {max-width:1580px; margin:0 auto;}
        .top_txt { display:inline-block; font-size:14px; color:#75777c; letter-spacing:1px; }
        h1 { padding:30px 0 0; font-size:54px; color:$white; font-weight:$regular; }
        .select_wrap {
            max-width:1580px;padding:80px 0 85px;margin:0 auto;
            ul {display:flex;justify-content:space-between;align-items:center;}
            li {
                width:13.5%; position:relative;
                span {
                    width:10px; height:10px; position:absolute; top:101px;
                    background-color:$white;left:50%; display:none; opacity:0;
                    @include transform(rotate(45deg));
                }
                &:nth-of-type(5), &:nth-of-type(6) {
                    .sb_box {right:0;left:auto;}
                }
                &.active {
                    & > button {
                        color:$white; border-color:$white;
                        &:after {
                            background:url(../../images/common/close_btn.png) 0 0 no-repeat;
                            @include transform(rotate(180deg));
                        }
                        //&:before {display:block;}
                    }
                }
                & > button {
                    width:100%;text-align:left;display:block;color:#d2ad83;font-size:14px;
                    font-family:$Play;padding:20px 0;border-bottom:1px solid #d2ad83; position:relative;
                    @include transition(all, .3s);
                    &:hover {
                        color:$white; border-color:$white;
                        &:after {background:url(../../images/common/close_btn.png) 0 0 no-repeat;}
                    }
                    &:after {
                        width:11px; height:6px;position:absolute; right:0; top:23px;display:block;content:"";
                        background:url(../../images/common/open_btn.png) 0 0 no-repeat; background-size:cover;
                        @include transition(all, .3s);
                    }
                }
            }
        }
        .filter_wrap {
            width:100%;position:absolute; top:550px; left:0;padding:0 50px;
            @include boxSizing;
            ul {
                max-width:1580px;min-width:1180px;position:relative;margin:0 auto;
                display:flex;justify-content:space-between;align-items:center;
            }
            li.sb_box {
                width:625px; height:350px;position:absolute; top:0; z-index:5;display:none;opacity:0;
                background-color:$white; box-shadow:5px 5px 60px rgba(132, 132, 132, 0.3);
                @include transform(translate(0, 20px));
                svg {
                    width:625px; height:350px;
                    g {
                        cursor:pointer;
                        &:hover {
                            path, line, polygon, polyline {stroke:$black;}
                        }
                        &.active {
                            path, line, polygon, polyline {stroke:$black;}
                            polygon {fill:$gold;}
                        }
                    }
                    polygon {fill:$white;}
                    path, line, polygon, polyline {
                        fill:transparent;stroke-width:1;stroke:#b18a68;
                        @include transition(all, .2s);
                    }
                    polygon {fill:$white;}
                }
                &:nth-of-type(1) {left:0;background:url(../../images/household/building_bg.jpg) 31% 58% no-repeat $white;}
                &:nth-of-type(2) { left:17.3%; }
                &:nth-of-type(3) { left:34.6%; }
                &:nth-of-type(4) { left:52%; }
                &:nth-of-type(5) { right:17%; }
                &:nth-of-type(6) { right:0; }
            }
            .btn_wrap {
                position:absolute; right:40px; bottom:40px;display:flex;
                button {
                    width:70px; height:70px;font-size:16px;border:1px solid $gold;background-color:$gold;
                    color:$white;font-weight:$regular; position:relative; overflow:hidden;
                    @include transition(all, .2s);
                    @include transform(skew(-0.03deg));
                    @include borderRadius(50px);
                    @include boxSizing;
                    &:hover {
                        &:nth-of-type(1) {border-color:$navy; color:$navy;}
                        &:nth-of-type(2) {
                            border-color:$navy;
                            &:after {width:100%; height:100%;}
                        }
                    }
                    &:nth-of-type(1) {margin:0 10px 0 0;background-color:$white;color:$gold;}
                    &:nth-of-type(2) {
                        border:none;
                        &:after {
                            width:0; height:0; top:50%; left:50%;position:absolute;content:"";display:block;
                            background-color:$navy;z-index:-1;
                            @include transform(translate(-50%, -50%));
                            @include transition(all, .2s);
                            @include borderRadius(70px);
                        }
                    }
                }
            }
        }
    }
    .sec2 {
        min-width:1180px;padding:0 50px;background-color:#f8f0ec;
        .row_list {
            padding:30px 0;max-width:1580px;margin:0 auto;border-bottom:1px solid #e1d9d5; cursor:pointer;
            &:last-of-type { border:none; }
            ul {display:flex;align-items:center;}
            li {
                text-align:center;
                &.room_number {
                    width:16.70%;color:$brown;text-align:left;
                    strong {font-size:58px;}
                    span {
                        font-size:18px;
                        &.building {position:relative;top:-3px;left:5px;margin:0 30px 0 0;}
                        &.room {position:relative;top:-1px;}
                    }
                }
                &.room_map {
                    width:36%;
                    .list_image {
                        width:fit-content;position:relative;margin:0 auto;
                        .slide_img {display:flex;width:240px;height:190px;overflow:hidden;}
                    }
                    .prev_btn, .next_btn {
                        width:28px; height:28px;position:absolute; top:90px;display:block;
                        background:url(../../images/common/room_btn.png) 0 0 no-repeat;
                        &:hover {
                            background:url(../../images/common/room_btn_over.png) 0 0 no-repeat;
                        }
                    }
                    .prev_btn {left:-45px;}
                    .next_btn {
                        right:-45px;
                        @include transform(rotate(180deg));
                    }
                }
                &.square_measure {
                    width:18.03%;text-align:left;display:flex;flex-direction:column;
                    font-family:$Play;align-items:center;
                    span {display:block;line-height:30px;color:$brown;font-size:18px;}
                    p {padding:20px 0 0;color:#a19894;font-size:14px;}
                }
                &.special {
                    width:19.55%;
                    @include transform(skew(-0.03deg));
                    span {
                        font-size:16.5px;font-weight:$bold;color:$brown;display:inline-block; padding:0 0 0 10px;
                        position:relative; margin:0 0 0 5px;line-height:30px;
                        &:before {content:"#";display:block;position:absolute; left:0;}
                    }
                }
                &.room_price {
                    width:16.07%;padding:0 50px 0 0;text-align:right;
                    p {
                        font-size:18px;font-weight:$bold;
                        &.price_number {font-family:$Play;font-weight:$regular;}
                        &.sold_out {color:#a19894;}
                    }
                }
            }
        }
    }
    .pop_container {position:fixed;width:0;height:100vh;z-index:11;top:0;right:0;overflow:hidden;}
    // House Hold Popup Container
    #housePopup {
        width:1280px; height:100%; min-height:865px; padding:90px 115px;
        position:absolute;right:0; top:0; background-color:$white;z-index:11;
        @include boxSizing;
        &:after {
            width:100%; height:150px;position:absolute; background-color:$navy;
            content:"";display:block;left:0;bottom:0;z-index:-1;
        }
        .cont {justify-content:space-between;display:flex;}
        .info_txt {
            min-width:300px; max-width:340px;
            .floor_txt {font-size:14px;color:#a19894;}
            strong {
                padding:10px 0 0;display:block;font-size:72px;color:$brown;
                span {
                    font-size:18px;
                    &.building {position:relative;top:-3px;left:5px;margin:0 20px 0 0;}
                    &.room {position:relative;top:-1px;}
                }
            }
            ul {
                padding:50px 0 0;
                @include transform(skew(-0.03deg));
            }
            li {
                padding:10px 0;font-size:15px;color:#a19894;display:flex;
                .hashtag {
                    display:inline-block; font-weight:$bold; margin:-4px 0 0 0;
                    span {
                        line-height:25px; display:inline-block; padding:0 0 0 10px; position:relative;
                        &:before {content:"#";display:block;position:absolute; left:0;}
                    }
                }
                i {
                    margin:0 5px 0 0;display:block; padding:0 8px 0 0; font-weight:$bold; position:relative;
                    &:after {content:":";display:block;position:absolute; right:0; top:0;}
                }
                span {display:inline-block;font-size:16px;color:$brown;}
            }
        }
        .button_wrap {
            width:190px; height:80px; display:flex; justify-content:space-between; position:absolute; top:550px;
            button {
                width:80px; height:80px;border:1px solid $gold;position:relative; /*overflow:hidden;*/
                @include borderRadius(80px);
                &:hover {
                    //border-color:transparent;
                    &:before {width:100%; height:100%;background-color:$gold;}
                    &:after {background:url(../../images/household/room_btn_over.png) 0 0 no-repeat;}
                }
                &:before, &:after {content:"";display:block;position:absolute;@include transition(all, .2s);}
                &:before {
                    width:0; height:0; top:50%; left:50%;z-index:1;
                    @include transform(translate(-50%,-50%));
                    @include borderRadius(80px);

                }
                &:after {
                    width:20px; height:14px;background-size:cover;top:33px;left:30px;z-index:2;
                    background:url(../../images/household/room_btn.png) 0 0 no-repeat;
                }
                &.next_btn { &:after {@include transform(rotate(180deg));} }
            }
        }
        .room_image {
            width:65%; margin:60px 0 0;display:flex;align-items:center; overflow:hidden; position:relative;
            height:500px;
            .room_img {
                width:100%; position:absolute; top:0; left:0; display:none; opacity:0;
                &:first-of-type { display:block; opacity:1; }
            }
            img {width:100%;}
        }
        .link_box {
            width:82%;display:flex;justify-content:space-between;position:absolute; left:50%;bottom:45px;
            @include transform(translate(-50%,0));
            a {
                color:$white;height:140px;padding:0 0 0 60px;display:flex;flex-direction:column;
                justify-content:center;font-weight:$bold; position:relative; z-index:1;
                @include boxSizing;
                &:hover { &:after{width:100%;} }
                &:after, &:before {display:block;content:"";position:absolute;}
                // Over Motion
                &:after{
                    width:0; height:100%;top:0; left:0;background-color:rgba(28, 28, 28, 0.4); z-index:-1;
                    @include transition(all, .5s);
                }
                // Arrow
                &:before {
                    width:20px; height:14px;content:"";display:block;position:absolute; right:50px; top:63px;
                    background:url(../../images/household/room_btn_over.png) 0 0 no-repeat; background-size:cover;
                    @include transform(rotate(180deg));
                }
                &:nth-of-type(1) {
                    width:62.13%;background-color:$gold;
                    i {color:#cebba9;display:block; padding:12px 0 0;font-weight:$regular; font-size:16px;}
                }
                &:nth-of-type(2) {width:36.99%;background-color:$brown;}
            }
        }
        .close_btn {
            width:120px; height:120px; display:block; background-color:#a8835b; position:absolute;
            left:-120px; top:0;
            @include transition(all, .2s);
            &:after {
                width:20px; height:20px; content:""; display:block; position:absolute; left:50%; top:50%;
                background:url(../../images/common/x_icon.png) 0 0 no-repeat; background-size:cover;
                @include transform(translate(-50%, -50%));
                @include transition(all, .2s);
            }
            &:hover {
                box-shadow:0 0 100px 100px rgba(5, 5, 5, .2) inset;
                &:after {@include transform(translate(-50%, -50%) rotate(90deg));}
            }
        }
    }
}




































